.paginator{
    margin-top: 50px;
    margin-right: auto;
    margin-left: auto;
    width: fit-content;
    align-items: center;
    text-align: center;
}

.page{
    width: 40px;
    height: 40px;
    margin: 0 20px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    color: greenyellow;
    cursor: pointer;
}

.page-active{
    border: 1px solid greenyellow;
}


